import { useState, useCallback, useMemo } from 'react';
import { useCedrosLogin } from '../context/useCedrosLogin';
import { ApiClient, handleApiError } from '../utils/apiClient';
import { validateEmail } from '../utils/validation';
import { silentWalletEnroll } from '../utils/silentWalletEnroll';
import { useRateLimiter } from './useRateLimiter';
import type { AuthResponse, AuthError } from '../types';
import type { MfaRequiredResponse } from '../types';

function isMfaRequiredResponse(
  response: AuthResponse | MfaRequiredResponse
): response is MfaRequiredResponse {
  return 'mfaRequired' in response && response.mfaRequired === true;
}

/** Result when MFA verification is required */
export interface MfaRequiredResult {
  mfaRequired: true;
  mfaToken: string;
  email: string;
  userId: string;
}

/** Result of successful login (no TOTP required or after TOTP verification) */
export interface LoginSuccessResult {
  mfaRequired: false;
  response: AuthResponse;
}

/** Union type for login result */
export type LoginResult = MfaRequiredResult | LoginSuccessResult;

export interface UseEmailAuthReturn {
  /** Login - may return mfaRequired if 2FA is enabled */
  login: (email: string, password: string) => Promise<LoginResult>;
  register: (email: string, password: string, name?: string) => Promise<AuthResponse>;
  isLoading: boolean;
  error: AuthError | null;
  clearError: () => void;
  /**
   * Number of remaining login attempts before rate limiting.
   *
   * M-10: Snapshot Behavior
   * This value is a point-in-time snapshot computed at render time.
   * It may be briefly stale during rapid requests or concurrent renders.
   * For UI display only - actual rate limiting is enforced inside login/register.
   */
  remainingAttempts: number;
  /**
   * Time in ms until rate limit resets (0 if not rate limited).
   *
   * M-10: Snapshot Behavior
   * This value is a point-in-time snapshot computed at render time.
   * It may be briefly stale - use for UI display, not for logic decisions.
   */
  timeUntilReset: number;
}

/**
 * Hook for email/password authentication.
 *
 * @example
 * ```tsx
 * function LoginForm() {
 *   const { login, isLoading, error } = useEmailAuth();
 *
 *   const handleSubmit = async (e) => {
 *     e.preventDefault();
 *     try {
 *       await login(email, password);
 *     } catch (err) {
 *       // Handle error
 *     }
 *   };
 * }
 * ```
 */
export function useEmailAuth(): UseEmailAuthReturn {
  const { config, _internal } = useCedrosLogin();
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState<AuthError | null>(null);

  // Rate limiter for login attempts (5 attempts per minute)
  const {
    checkLimit,
    getRemainingAttempts,
    getTimeUntilReset,
    reset: resetRateLimit,
  } = useRateLimiter({ maxAttempts: 5, windowMs: 60000 });

  const apiClient = useMemo(
    () =>
      new ApiClient({
        baseUrl: config.serverUrl,
        timeoutMs: config.requestTimeout,
        retryAttempts: config.retryAttempts,
      }),
    [config.serverUrl, config.requestTimeout, config.retryAttempts]
  );

  const callbacks = config.callbacks;
  const walletEnrollmentEnabled = config.features?.walletEnrollment !== false;
  const serverUrl = config.serverUrl;

  const login = useCallback(
    async (email: string, password: string): Promise<LoginResult> => {
      // Validate email format before API call
      if (!validateEmail(email)) {
        const validationError: AuthError = {
          code: 'VALIDATION_ERROR',
          message: 'Please enter a valid email address',
        };
        setError(validationError);
        throw validationError;
      }

      // UI-7: Rate limit is checked BEFORE API call intentionally.
      // This prevents brute force attacks by limiting attempt frequency,
      // not just successful request frequency.
      try {
        checkLimit();
      } catch (err) {
        const rateLimitError: AuthError = {
          code: 'RATE_LIMITED',
          message: err instanceof Error ? err.message : 'Too many attempts',
        };
        setError(rateLimitError);
        throw rateLimitError;
      }

      setIsLoading(true);
      setError(null);

      try {
        const data = await apiClient.post<AuthResponse | MfaRequiredResponse>('/login', {
          email,
          password,
        });

        // Check if MFA verification is required
        if (isMfaRequiredResponse(data)) {
          return {
            mfaRequired: true,
            mfaToken: data.mfaToken,
            email,
            userId: data.userId,
          };
        }

        // Normal login success
        const authResponse: AuthResponse = data;
        callbacks?.onLoginSuccess?.(authResponse.user, 'email');
        _internal?.handleLoginSuccess(authResponse.user, authResponse.tokens);
        resetRateLimit(); // Reset on successful login
        return {
          mfaRequired: false,
          response: authResponse,
        };
      } catch (err) {
        const authError = handleApiError(err, 'Login failed');
        setError(authError);
        throw authError;
      } finally {
        setIsLoading(false);
      }
    },
    [apiClient, callbacks, _internal, checkLimit, resetRateLimit]
  );

  const register = useCallback(
    async (email: string, password: string, name?: string): Promise<AuthResponse> => {
      // Validate email format before API call
      if (!validateEmail(email)) {
        const validationError: AuthError = {
          code: 'VALIDATION_ERROR',
          message: 'Please enter a valid email address',
        };
        setError(validationError);
        throw validationError;
      }

      // UI-7: Rate limit is checked BEFORE API call intentionally.
      // This prevents brute force attacks by limiting attempt frequency,
      // not just successful request frequency.
      try {
        checkLimit();
      } catch (err) {
        const rateLimitError: AuthError = {
          code: 'RATE_LIMITED',
          message: err instanceof Error ? err.message : 'Too many attempts',
        };
        setError(rateLimitError);
        throw rateLimitError;
      }

      setIsLoading(true);
      setError(null);

      try {
        const data = await apiClient.post<AuthResponse>('/register', { email, password, name });
        callbacks?.onLoginSuccess?.(data.user, 'email');
        _internal?.handleLoginSuccess(data.user, data.tokens);
        resetRateLimit(); // Reset on successful registration

        // Auto-enroll embedded wallet in background (don't block registration)
        // Uses password for Share A encryption. Recovery phrase can be retrieved later.
        if (walletEnrollmentEnabled) {
          const accessToken = data.tokens?.accessToken ?? '';
          silentWalletEnroll({
            password,
            serverUrl,
            accessToken,
          }).then((result) => {
            if (result.success) {
              console.log('[useEmailAuth] Wallet auto-enrolled:', result.solanaPubkey);
            } else {
              console.warn('[useEmailAuth] Wallet auto-enrollment failed:', result.error);
            }
          });
        }

        return data;
      } catch (err) {
        const authError = handleApiError(err, 'Registration failed');
        setError(authError);
        throw authError;
      } finally {
        setIsLoading(false);
      }
    },
    [
      apiClient,
      callbacks,
      _internal,
      checkLimit,
      resetRateLimit,
      serverUrl,
      walletEnrollmentEnabled,
    ]
  );

  const clearError = useCallback(() => setError(null), []);

  return {
    login,
    register,
    isLoading,
    error,
    clearError,
    // M-10: Point-in-time snapshots for UI display (see interface JSDoc)
    remainingAttempts: getRemainingAttempts(),
    timeUntilReset: getTimeUntilReset(),
  };
}
