/**
 * Shim for Node's `crypto` module.
 * Maps `require('crypto')` / `import 'crypto'` to Web Crypto API in the browser.
 * Used by vite.config.ts alias to support CJS dependencies that expect Node crypto.
 */

// Re-export Web Crypto API
export default globalThis.crypto;
export const webcrypto = globalThis.crypto;
export const subtle = globalThis.crypto.subtle;
export const getRandomValues = globalThis.crypto.getRandomValues.bind(globalThis.crypto);
export const randomUUID = globalThis.crypto.randomUUID.bind(globalThis.crypto);
