/**
 * Silent wallet enrollment utility
 *
 * Performs wallet enrollment in the background without UI interaction.
 * Used for auto-enrolling wallets during registration.
 *
 * Security: Uses the same Shamir Secret Sharing scheme as manual enrollment.
 * The recovery phrase is not shown - user can retrieve it later if needed.
 */

import {
  generateSeed,
  generateArgon2Salt,
  splitSecret,
  argon2DeriveInWorker,
  aesGcmEncryptToBase64,
  uint8ArrayToBase64,
  getPublicKeyFromSeed,
  publicKeyToBase58,
  wipeAll,
  toEncryptionKey,
  DEFAULT_KDF_PARAMS,
} from '../crypto';
import { ApiClient } from './apiClient';
import type { WalletEnrollRequest } from '../types/wallet';

export interface SilentEnrollOptions {
  /** User's password for Share A encryption */
  password: string;
  /** Server base URL */
  serverUrl: string;
  /** Access token for authentication (optional if using cookies) */
  accessToken?: string;
  /** Request timeout in ms */
  timeoutMs?: number;
}

export interface SilentEnrollResult {
  success: boolean;
  solanaPubkey?: string;
  error?: string;
}

/**
 * Silently enroll a wallet for a user
 *
 * This function performs the complete wallet enrollment process:
 * 1. Generate 32-byte seed
 * 2. Split into 3 Shamir shares (threshold 2)
 * 3. Encrypt Share A with password-derived key (Argon2id)
 * 4. Derive Solana public key from seed
 * 5. Upload encrypted Share A + plaintext Share B to server
 *
 * The recovery phrase (Share C) is not returned - user can recover it
 * later through the wallet recovery flow if needed.
 *
 * @param options - Enrollment options
 * @returns Result with success status and Solana public key
 */
export async function silentWalletEnroll(
  options: SilentEnrollOptions
): Promise<SilentEnrollResult> {
  const { password, serverUrl, accessToken, timeoutMs = 30000 } = options;

  // Track sensitive data for cleanup
  const sensitiveData: Uint8Array[] = [];

  try {
    // Step 1: Generate seed
    const seed = generateSeed();
    sensitiveData.push(seed);

    // Step 2: Split into shares
    const { shareA, shareB } = splitSecret(seed);
    sensitiveData.push(shareA, shareB);

    // Step 3: Derive encryption key from password
    const salt = generateArgon2Salt();
    const key = await argon2DeriveInWorker(password, salt, DEFAULT_KDF_PARAMS);
    sensitiveData.push(key);

    // Step 4: Encrypt Share A
    const encryptedA = await aesGcmEncryptToBase64(shareA, toEncryptionKey(key));

    // Step 5: Derive Solana public key
    const publicKey = getPublicKeyFromSeed(seed);
    const solanaPubkey = publicKeyToBase58(publicKey);

    // Step 6: Build enrollment request
    // Include recovery data (seed) for server to store if recovery mode is enabled
    const request: WalletEnrollRequest = {
      solanaPubkey,
      shareAAuthMethod: 'password',
      shareACiphertext: encryptedA.ciphertext,
      shareANonce: encryptedA.nonce,
      shareB: uint8ArrayToBase64(shareB),
      shareAKdfSalt: uint8ArrayToBase64(salt),
      shareAKdfParams: DEFAULT_KDF_PARAMS,
      // Send seed as recovery data - server will store based on recovery mode
      recoveryData: uint8ArrayToBase64(seed),
    };

    // Step 7: Upload to server
    // If accessToken provided, use it; otherwise rely on cookies
    const apiClient = new ApiClient({
      baseUrl: serverUrl,
      timeoutMs,
      getAccessToken: accessToken ? () => accessToken : undefined,
    });

    await apiClient.post('/wallet/enroll', request);

    return {
      success: true,
      solanaPubkey,
    };
  } catch (err) {
    const errorMessage = err instanceof Error ? err.message : 'Wallet enrollment failed';
    // Log but don't throw - silent enrollment failure shouldn't break registration
    console.warn('[silentWalletEnroll] Failed:', errorMessage);
    return {
      success: false,
      error: errorMessage,
    };
  } finally {
    // Always wipe sensitive data
    wipeAll(...sensitiveData);
  }
}
