import { useState, useId, type InputHTMLAttributes } from 'react';
import { validatePassword } from '../../utils/validation';
import type { PasswordValidation } from '../../types';

export interface PasswordInputProps extends Omit<InputHTMLAttributes<HTMLInputElement>, 'type'> {
  label?: string;
  /** Action element shown on the right side of the label (e.g., "Forgot password?" link) */
  labelAction?: React.ReactNode;
  showStrengthMeter?: boolean;
  onValidationChange?: (validation: PasswordValidation) => void;
  error?: string;
}

/**
 * Password input with visibility toggle and optional strength meter
 */
export function PasswordInput({
  label = 'Password',
  labelAction,
  showStrengthMeter = false,
  onValidationChange,
  error,
  className = '',
  onChange,
  value,
  ...props
}: PasswordInputProps) {
  const [showPassword, setShowPassword] = useState(false);
  const [validation, setValidation] = useState<PasswordValidation | null>(null);
  const inputId = useId();

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const newValue = e.target.value;

    if (showStrengthMeter || onValidationChange) {
      const newValidation = validatePassword(newValue);
      setValidation(newValidation);
      onValidationChange?.(newValidation);
    }

    onChange?.(e);
  };

  const strengthColors = {
    weak: 'var(--cedros-destructive, #ef4444)',
    fair: 'var(--cedros-warning, #f59e0b)',
    good: 'var(--cedros-success, #22c55e)',
    strong: 'var(--cedros-success, #22c55e)',
  };

  return (
    <div className={`cedros-password-input ${className}`}>
      <div className="cedros-label-row">
        <label htmlFor={inputId} className="cedros-label">
          {label}
        </label>
        {labelAction}
      </div>
      <div className="cedros-password-wrapper">
        <input
          id={inputId}
          type={showPassword ? 'text' : 'password'}
          className="cedros-input"
          onChange={handleChange}
          value={value}
          aria-invalid={error ? 'true' : undefined}
          aria-describedby={error ? `${inputId}-error` : undefined}
          {...props}
        />
        <button
          type="button"
          className="cedros-password-toggle"
          onClick={() => setShowPassword(!showPassword)}
          aria-label={showPassword ? 'Hide password' : 'Show password'}
          aria-pressed={showPassword}
        >
          {showPassword ? (
            <svg width="20" height="20" viewBox="0 0 20 20" fill="none" aria-hidden="true">
              <path
                d="M2.5 10s3-6 7.5-6 7.5 6 7.5 6-3 6-7.5 6-7.5-6-7.5-6z"
                stroke="currentColor"
                strokeWidth="1.5"
              />
              <circle cx="10" cy="10" r="2.5" stroke="currentColor" strokeWidth="1.5" />
              <path d="M3 17L17 3" stroke="currentColor" strokeWidth="1.5" strokeLinecap="round" />
            </svg>
          ) : (
            <svg width="20" height="20" viewBox="0 0 20 20" fill="none" aria-hidden="true">
              <path
                d="M2.5 10s3-6 7.5-6 7.5 6 7.5 6-3 6-7.5 6-7.5-6-7.5-6z"
                stroke="currentColor"
                strokeWidth="1.5"
              />
              <circle cx="10" cy="10" r="2.5" stroke="currentColor" strokeWidth="1.5" />
            </svg>
          )}
        </button>
      </div>

      {error && (
        <p id={`${inputId}-error`} className="cedros-input-error">
          {error}
        </p>
      )}

      {showStrengthMeter && validation && (value as string)?.length > 0 && (
        <div className="cedros-password-strength">
          <div className="cedros-strength-bar">
            <div
              className="cedros-strength-fill"
              style={{
                width: `${
                  validation.strength === 'weak'
                    ? 25
                    : validation.strength === 'fair'
                      ? 50
                      : validation.strength === 'good'
                        ? 75
                        : 100
                }%`,
                backgroundColor: strengthColors[validation.strength],
              }}
            />
          </div>
          <span className="cedros-strength-label">{validation.strength}</span>
        </div>
      )}
    </div>
  );
}
