import { useState, type FormEvent } from 'react';
import { useCedrosLogin } from '../../context/useCedrosLogin';
import { useEmailAuth } from '../../hooks/useEmailAuth';
import { PasswordInput } from './PasswordInput';
import { LoadingSpinner } from '../shared/LoadingSpinner';
import { ErrorMessage } from '../shared/ErrorMessage';
import { sanitizeExternalUrl } from '../../utils/sanitization';
import type { PasswordValidation, AuthError } from '../../types';

export interface EmailRegisterFormProps {
  onSuccess?: () => void;
  onSwitchToLogin?: () => void;
  className?: string;
}

/** Values collected from the registration form (for callback) */
export interface RegistrationData {
  termsAccepted: boolean;
  emailOptIn: boolean;
}

/**
 * Email/password registration form
 */
export function EmailRegisterForm({
  onSuccess,
  onSwitchToLogin,
  className = '',
}: EmailRegisterFormProps) {
  const { config } = useCedrosLogin();
  const { register, isLoading, error, clearError } = useEmailAuth();
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [passwordValidation, setPasswordValidation] = useState<PasswordValidation | null>(null);
  const [termsError, setTermsError] = useState<AuthError | null>(null);

  // Get form configuration
  const termsConfig = config.forms?.termsOfService;
  const emailOptInConfig = config.forms?.emailOptIn;

  const showTerms = termsConfig?.show ?? false;
  const termsRequired = termsConfig?.required ?? true;
  const termsDefaultChecked = termsConfig?.defaultChecked ?? false;
  const termsLabel = termsConfig?.label ?? 'I agree to the Terms of Service';
  const termsUrl = termsConfig?.url;
  const safeTermsUrl = sanitizeExternalUrl(termsUrl);

  const showEmailOptIn = emailOptInConfig?.show ?? false;
  const emailOptInDefaultChecked = emailOptInConfig?.defaultChecked ?? false;
  const emailOptInLabel = emailOptInConfig?.label ?? 'Send me updates and news';

  // Initialize checkbox states with defaults
  const [termsAccepted, setTermsAccepted] = useState(termsDefaultChecked);
  const [emailOptIn, setEmailOptIn] = useState(emailOptInDefaultChecked);

  const passwordsMatch = password === confirmPassword;
  const isPasswordValid = passwordValidation?.isValid ?? false;

  // Terms must be accepted if shown and required
  const termsValid = !showTerms || !termsRequired || termsAccepted;

  const canSubmit =
    email &&
    password &&
    confirmPassword &&
    passwordsMatch &&
    isPasswordValid &&
    termsValid &&
    !isLoading;

  const handleSubmit = async (e: FormEvent) => {
    e.preventDefault();

    // Clear any previous terms error
    setTermsError(null);

    // Validate terms if required
    if (showTerms && termsRequired && !termsAccepted) {
      setTermsError({
        code: 'VALIDATION_ERROR',
        message: 'You must agree to the Terms of Service to continue',
      });
      return;
    }

    if (!canSubmit) return;

    try {
      // Note: termsAccepted and emailOptIn values can be passed to the backend
      // via the register function if needed. For now, we call register with the
      // standard params, but the backend could be extended to accept these values.
      await register(email, password, name || undefined);
      onSuccess?.();
    } catch {
      // Error is handled by the hook
    }
  };

  const combinedError = error || termsError;
  const combinedClearError = () => {
    clearError();
    setTermsError(null);
  };

  return (
    <form onSubmit={handleSubmit} className={`cedros-form ${className}`}>
      <div className="cedros-form-field">
        <label htmlFor="name" className="cedros-label">
          Name <span className="cedros-optional">(optional)</span>
        </label>
        <input
          id="name"
          type="text"
          className="cedros-input"
          value={name}
          onChange={(e) => setName(e.target.value)}
          placeholder="Your name"
          autoComplete="name"
          disabled={isLoading}
        />
      </div>

      <div className="cedros-form-field">
        <label htmlFor="register-email" className="cedros-label">
          Email
        </label>
        <input
          id="register-email"
          type="email"
          className="cedros-input"
          value={email}
          onChange={(e) => setEmail(e.target.value)}
          placeholder="you@example.com"
          required
          aria-required="true"
          autoComplete="email"
          disabled={isLoading}
        />
      </div>

      <div className="cedros-form-field">
        <PasswordInput
          value={password}
          onChange={(e) => setPassword(e.target.value)}
          placeholder="Create a password"
          required
          autoComplete="new-password"
          disabled={isLoading}
          showStrengthMeter
          onValidationChange={setPasswordValidation}
        />
      </div>

      <div className="cedros-form-field">
        <label htmlFor="confirm-password" className="cedros-label">
          Confirm Password
        </label>
        <input
          id="confirm-password"
          type="password"
          className="cedros-input"
          value={confirmPassword}
          onChange={(e) => setConfirmPassword(e.target.value)}
          placeholder="Confirm your password"
          required
          aria-required="true"
          autoComplete="new-password"
          disabled={isLoading}
          aria-invalid={confirmPassword && !passwordsMatch ? 'true' : undefined}
          aria-describedby={
            confirmPassword && !passwordsMatch ? 'confirm-password-error' : undefined
          }
        />
        {confirmPassword && !passwordsMatch && (
          <p id="confirm-password-error" className="cedros-input-error" role="alert">
            Passwords do not match
          </p>
        )}
      </div>

      {/* Terms of Service checkbox */}
      {showTerms && (
        <div className="cedros-form-field cedros-checkbox-field">
          <label className="cedros-checkbox-label">
            <input
              type="checkbox"
              className="cedros-checkbox"
              checked={termsAccepted}
              onChange={(e) => setTermsAccepted(e.target.checked)}
              disabled={isLoading}
              aria-required={termsRequired}
            />
            <span className="cedros-checkbox-text">
              {safeTermsUrl ? (
                <>
                  {termsLabel.replace('Terms of Service', '').trim() || 'I agree to the'}{' '}
                  <a
                    href={safeTermsUrl}
                    target="_blank"
                    rel="noopener noreferrer"
                    className="cedros-link"
                  >
                    Terms of Service
                  </a>
                </>
              ) : (
                termsLabel
              )}
              {termsRequired && <span className="cedros-required">*</span>}
            </span>
          </label>
        </div>
      )}

      {/* Email opt-in checkbox */}
      {showEmailOptIn && (
        <div className="cedros-form-field cedros-checkbox-field">
          <label className="cedros-checkbox-label">
            <input
              type="checkbox"
              className="cedros-checkbox"
              checked={emailOptIn}
              onChange={(e) => setEmailOptIn(e.target.checked)}
              disabled={isLoading}
            />
            <span className="cedros-checkbox-text">{emailOptInLabel}</span>
          </label>
        </div>
      )}

      <ErrorMessage error={combinedError} onDismiss={combinedClearError} />

      <button
        type="submit"
        className="cedros-button cedros-button-primary cedros-button-md cedros-button-full"
        disabled={!canSubmit}
        aria-busy={isLoading}
      >
        {isLoading ? (
          <>
            <LoadingSpinner size="sm" announce label="Creating account" />
            <span>Creating account...</span>
          </>
        ) : (
          'Create account'
        )}
      </button>

      {onSwitchToLogin && (
        <p className="cedros-form-footer">
          Already have an account?{' '}
          <button type="button" className="cedros-link" onClick={onSwitchToLogin}>
            Sign in
          </button>
        </p>
      )}
    </form>
  );
}
