import { useState, type FormEvent } from 'react';
import { useCedrosLogin } from '../../context/useCedrosLogin';
import { useEmailAuth } from '../../hooks/useEmailAuth';
import { useInstantLink } from '../../hooks/useInstantLink';
import { PasswordInput } from './PasswordInput';
import { LoadingSpinner } from '../shared/LoadingSpinner';
import { ErrorMessage } from '../shared/ErrorMessage';
import { TotpVerify } from '../totp/TotpVerify';
// COMP-01: Email validation now handled by useInstantLink hook internally

export interface EmailLoginFormProps {
  onSuccess?: () => void;
  onSwitchToRegister?: () => void;
  /** Called when user clicks "Forgot password?" (only in 'reset' mode) */
  onForgotPassword?: () => void;
  className?: string;
}

/**
 * Email/password login form
 */
export function EmailLoginForm({
  onSuccess,
  onSwitchToRegister,
  onForgotPassword,
  className = '',
}: EmailLoginFormProps) {
  const { config } = useCedrosLogin();
  const { login, isLoading, error, clearError } = useEmailAuth();
  const {
    sendInstantLink,
    isLoading: isInstantLinkLoading,
    isSuccess: isInstantLinkSuccess,
    error: instantLinkError,
    clearError: clearInstantLinkError,
    reset: resetInstantLink,
  } = useInstantLink();
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  // MFA state for 2FA flow
  const [mfaToken, setMfaToken] = useState<string | null>(null);
  const [mfaEmail, setMfaEmail] = useState<string>('');

  const forgotPasswordMode = config.forms?.forgotPassword?.mode ?? 'reset';

  const handleSubmit = async (e: FormEvent) => {
    e.preventDefault();
    try {
      const result = await login(email, password);
      if (result.mfaRequired) {
        // MFA verification needed
        setMfaToken(result.mfaToken);
        setMfaEmail(result.email);
      } else {
        // Login successful
        onSuccess?.();
      }
    } catch {
      // Error is handled by the hook
    }
  };

  const handleTotpSuccess = () => {
    setMfaToken(null);
    setMfaEmail('');
    onSuccess?.();
  };

  const handleTotpBack = () => {
    setMfaToken(null);
    setMfaEmail('');
    setPassword(''); // Clear password for security
  };

  const handleForgotPassword = async () => {
    if (forgotPasswordMode === 'instantLink') {
      // COMP-01: In instant link mode, call sendInstantLink directly.
      // The hook validates email and sets error state if invalid.
      try {
        await sendInstantLink(email);
      } catch {
        // Error handled by hook's error state
      }
    } else {
      // In reset mode, call the callback
      onForgotPassword?.();
    }
  };

  // Show TOTP verification step
  if (mfaToken) {
    return (
      <TotpVerify
        mfaToken={mfaToken}
        email={mfaEmail}
        onSuccess={handleTotpSuccess}
        onBack={handleTotpBack}
        className={className}
      />
    );
  }

  // Show instant link success state
  if (isInstantLinkSuccess) {
    return (
      <div className={`cedros-instant-link-success ${className}`}>
        <svg
          className="cedros-success-icon"
          width="48"
          height="48"
          viewBox="0 0 48 48"
          fill="none"
          aria-hidden="true"
        >
          <circle cx="24" cy="24" r="22" stroke="currentColor" strokeWidth="2" />
          <path
            d="M14 24l7 7 13-13"
            stroke="currentColor"
            strokeWidth="2"
            strokeLinecap="round"
            strokeLinejoin="round"
          />
        </svg>
        <h3 className="cedros-success-title">Check your email</h3>
        <p className="cedros-success-message">
          We sent a sign-in link to <strong>{email}</strong>. Click the link to sign in.
        </p>
        <button
          type="button"
          className="cedros-button cedros-button-md cedros-button-outline"
          onClick={resetInstantLink}
        >
          Back to login
        </button>
      </div>
    );
  }

  const combinedError = error || instantLinkError;
  const combinedClearError = () => {
    clearError();
    clearInstantLinkError();
  };
  const isAnyLoading = isLoading || isInstantLinkLoading;

  return (
    <form onSubmit={handleSubmit} className={`cedros-form ${className}`}>
      <div className="cedros-form-field">
        <label htmlFor="email" className="cedros-label">
          Email
        </label>
        <input
          id="email"
          type="email"
          className="cedros-input"
          value={email}
          onChange={(e) => setEmail(e.target.value)}
          placeholder="you@example.com"
          required
          aria-required="true"
          autoComplete="email"
          disabled={isAnyLoading}
        />
      </div>

      <div className="cedros-form-field">
        <PasswordInput
          value={password}
          onChange={(e) => setPassword(e.target.value)}
          placeholder="Enter your password"
          required
          autoComplete="current-password"
          disabled={isAnyLoading}
          labelAction={
            onForgotPassword || forgotPasswordMode === 'instantLink' ? (
              <button
                type="button"
                className="cedros-link cedros-link-sm"
                onClick={handleForgotPassword}
                disabled={isInstantLinkLoading}
              >
                {isInstantLinkLoading ? 'Sending...' : 'Forgot your password?'}
              </button>
            ) : undefined
          }
        />
      </div>

      <ErrorMessage error={combinedError} onDismiss={combinedClearError} />

      <button
        type="submit"
        className="cedros-button cedros-button-primary cedros-button-md cedros-button-full"
        disabled={isAnyLoading || !email || !password}
        aria-busy={isLoading}
      >
        {isLoading ? (
          <>
            <LoadingSpinner size="sm" announce label="Signing in" />
            <span>Signing in...</span>
          </>
        ) : (
          'Sign in'
        )}
      </button>

      {onSwitchToRegister && (
        <p className="cedros-form-footer">
          Don&apos;t have an account?{' '}
          <button type="button" className="cedros-link" onClick={onSwitchToRegister}>
            Sign up
          </button>
        </p>
      )}
    </form>
  );
}
